.container {
  max-width: 500px;
  margin: auto;
  width: 100%;
}

.game {
  padding: 20px;
  background: #fefefe;
  margin-bottom: 20px;
  border: 1px solid #f0f0f0;
  border-radius: 3px;
  width: 100%;
  & > *:last-child {
    margin-bottom: 0;
  }
}

.removeBtn {
  cursor: pointer;
  float: right;
  margin: 6px 0 0 10px;
  font-size: 20px;
}
